<template>
	<gracePage :customHeader="false">
		<view slot="gBody" class="grace-body">
			<view v-if="userIsLogin">
				<view class="grace-margin-top">当前登录用户：{{ userInfo.UserName }}</view>
				<!-- <view class="ucenter-line"></view> -->

				<!-- <view class="grace-list grace-title"> -->

				<view class="grace-title">
					<view class="icons grace-icons icon-set grace-yellow"></view>
					<view class="current-project">当前项目：</view>

					<picker mode="selector" @change="bindPickerChangeProject" v-model="projectIndex" :range="projectList" range-key="Name" name="Name">
						<view class="uni-input">{{ projectList[projectIndex].Name }}</view>
					</picker>
					<view class="grace-icons icon-arrow-down"></view>
				</view>

				<view class="grace-card-view">
					<view class="body">
						<view v-if="currentProjectInfo.OrderStatusCode == 100" class="img">
							<image src="../../static/icon/icon-wms-done.png" />
							<view>已结束</view>
						</view>
						<view v-else-if="currentProjectInfo.OrderStatusCode == 20" class="img">
							<image src="../../static/icon/icon-wms-todo.png" />
							<view>进行中</view>
						</view>
						<view v-else class="img">
							<image src="../../static/icon/icon-wms-todo.png" />
							<view>未开始</view>
						</view>

						<view class="desc">
							<view class="grace-title">盘点类型：{{ currentProjectInfo.OrderTypeName }}</view>
							<view class="grace-text">开始时间：{{ currentProjectInfo.PlannedStartTime }}</view>
							<view class="grace-text">结束时间：{{ currentProjectInfo.PlannedEndTime }}</view>
						</view>
					</view>
				</view>

				<!-- <view class="ucenter-line"></view> -->
				<view class="grace-margin-top grace-title">当前盘点总进度：</view>
				<view style="width:150px">
					<graceCirProgress :width="150" canvasId="graceCirProgress1" :value="projectProgress" :lineWidth="20" progressColor="#10AEFF" fontColor="#10AEFF">
						<canvas style="width:150px; height:250px;" canvas-id="graceCirProgress1"></canvas>
					</graceCirProgress>
				</view>
				<view class="ucenter-line"></view>

				<view class="grace-margin-top grace-body">
					初盘进度：
					<progress :percent="firstProgress" show-info />
				</view>
				<view v-if="projectStatistic.IsSecondEnabled == 1" class="grace-margin-top grace-body">
					复盘盘进度：
					<progress :percent="secondProgress" show-info />
				</view>
				<view v-if="projectStatistic.IsThirdEnabled == 1" class="grace-margin-top grace-body">
					三盘进度：
					<progress :percent="thirdProgess" show-info />
				</view>

				<view class="ucenter-line"></view>

				<view class="grace-title">
					<view class="grace-title">按分类查看进度：</view>
					<picker mode="selector" @change="bindPickerChangeChart" v-model="chartIndex" :range="chartList" range-key="Name" name="Name">
						<view class="uni-input">{{ chartList[chartIndex].Name }}</view>
					</picker>
					<view class="grace-icons icon-arrow-down"></view>
				</view>

				<view class="grace-margin-top grace-body">
					{{}}：
					<progress :percent="firstProgress" show-info />
				</view>

				<view class="grace-margin grace-margin-bottom">
					<navigator url="../user/contactus" class="contact grace-border grace-border-radius">有任何技术问题，请联系：牛逼的崔老师</navigator>
				</view>
				<view class="grace-margin-top grace-margin-bottom"></view>
			</view>
			<view v-if="!userIsLogin" class="hello">
				<view class="grace-margin-top grace-margin-bottom"></view>
				<view class="img grace-flex-center grace-wrap"><image src="../../static/img/logo.png" mode="widthFix" style="width: 100px;"></image></view>
				<view class="title">
					<view>您好，游客。</view>
					<view>请登录后使用本系统。</view>
				</view>
			</view>
		</view>
	</gracePage>
</template>

<script>
import graceCirProgress from '../../graceUI/components/graceCirProgress.vue';
export default {
	data() {
		return {
			userIsLogin: false,
			userInfo: {},
			projectList: [{}],
			projectIndex: 0,
			currentProjectInfo: {},

			projectStatistic: {},

			projectProgress: 0,
			firstProgress: 0,
			secondProgress: 0,
			thirdProgess: 0,
			//{Name: "操作员进度", value: 'OperatorUserName'} OperatorUserName,LeaderUserName,Company,Site,Project,WarehouseCode
			chartList: [{ Name: '操作员进度', value: 'OperatorUserName' }, { Name: '仓库进度', value: 'WarehouseCode' }],
			chartIndex: 0,
			chartRenderList: []
		};
	},
	components: {
		graceCirProgress
	},
	onLoad() {
		this.onShow;
	},
	onShow() {
		var me = this;
		var userInfo = me.getCurrentUserInfo();
		if (userInfo != null) {
			me.userIsLogin = true;
			me.userInfo = userInfo;
			var serverUrl = me.getServerUrl();
			//读取ProjectList
			uni.request({
				url: serverUrl + '/PhysicalInventoryOrder/GetList',
				method: 'GET',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							if (res.data.Data.length > 0) {
								//如果projectList不为空，则绑定
								me.projectList = res.data.Data;
								//console.log(res.data.Data);
								//如果Storage里面的project不为空，则下拉菜单显示storage里的project,但是要更新内容
								var currentProjectInfo = me.getCurrentProjectInfo();
								if (currentProjectInfo != null) {
									for (let i = 0; i < me.projectList.length; i++) {
										if (currentProjectInfo.Id === me.projectList[i].Id) {
											me.projectIndex = i;
											//刷新me.currentProjectInfo信息并保存到内存
											me.currentProjectInfo = me.projectList[i];
											uni.setStorageSync('currentProjectInfo', me.projectList[i]);
										}
									}
								} else {
									//如果storage里面的project为空，则找到最新的project显示，并把此project保存到storage
									me.currentProjectInfo = me.projectList[0];
									uni.setStorageSync('currentProjectInfo', me.projectList[0]);
								}
								//获取Statistics数据，显示进度环，显示进度
								// me.getStatistics(me.currentProjectInfo.Id);

								uni.request({
									url: serverUrl + '/PhysicalInventoryOrder/GetStatistics/' + me.currentProjectInfo.Id,
									method: 'POST',
									header: {
										appKey: me.appKey,
										appSecret: me.appSecret,
										userId: userInfo.Id,
										openId: userInfo.OpenId
									},
									success: res => {
										if (res.data.Status) {
											if (res.data.StatusCode == 'UserNotLogin') {
												uni.removeStorageSync('currentUserInfo');
												me.userIsLogin = false;
												uni.showToast({
													title: '用户已异地登录',
													duration: 2000,
													icon: 'none'
												});
											} else {
												if (res.data.Data == null) {
													uni.showToast({
														title: '无统计数据',
														duration: 2000,
														icon: 'none'
													});
													me.projectStatistic = {};
												} else {
													me.projectStatistic = res.data.Data;
													if (me.projectStatistic.TotalLines > 0) {
														me.projectProgress = Math.ceil((me.projectStatistic.CountedLines / me.projectStatistic.TotalLines) * 100);
													} else {
														me.projectProgress = 0;
													}
													if (me.projectStatistic.FirstTotalLines > 0) {
														me.firstProgress = Math.ceil((me.projectStatistic.FirstCountedLines / me.projectStatistic.FirstTotalLines) * 100);
													} else {
														me.firstProgress = 0;
													}
													if (me.projectStatistic.IsSecondEnabled == 1) {
														if (me.projectStatistic.SecondTotalLines > 0) {
															me.secondProgress = Math.ceil((me.projectStatistic.SecondCountedLines / me.projectStatistic.SecondTotalLines) * 100);
														} else {
															me.secondProgress = 0;
														}
													}
													if (me.projectStatistic.IsThirdEnabled == 1) {
														if (me.projectStatistic.FirstTotalLines > 0) {
															me.thirdProgess = Math.ceil((me.projectStatistic.ThirdCountedLines / me.projectStatistic.FirstTotalLines) * 100);
														} else {
															me.thirdProgess = 0;
														}
													}
													//console.log(me.projectStatistic);
												}
											}
										} else {
											//debugger;
											uni.showToast({
												title: res.data.StatusMessage,
												duration: 2000,
												icon: 'none'
											});
											//return;
										}
									},
									fail: function() {
										uni.showToast({
											title: '网络异常请重试',
											duration: 2000,
											icon: 'none'
										});
									},
									complete: function() {
										setTimeout(function() {
											uni.hideLoading();
										}, 2000);
									}
								});
								//获取LineStatistics，按照一二三盘显示数据
								if (me.currentProjectInfo.IsCompanyEnabled == 1 && me.chartList.indexOf({ Name: '公司进度', value: 'Company' }) == -1) {
									me.chartList.push({ Name: '公司进度', value: 'Company' });
								}
								if (me.currentProjectInfo.IsSiteEnabled == 1 && me.chartList.indexOf({ Name: '分公司进度', value: 'Site' }) == -1) {
									me.chartList.push({ Name: '分公司进度', value: 'Site' });
								}
								if (me.currentProjectInfo.IsProjectEnabled == 1 && me.chartList.indexOf({ Name: '产品项目进度', value: 'Project' }) == -1) {
									me.chartList.push({ Name: '产品项目进度', value: 'Project' });
								}
							} else {
								//如果项目列表为空，则提示尚未有盘点项目生成，清空currentProjectInfo，不能进入
								uni.removeStorageSync('currentProjectInfo');
								uni.showToast({
									title: '暂无盘点项目',
									duration: 2000,
									icon: 'none'
								});
								setTimeout(function() {
									uni.switchTab({
										url: '../user/usercenter'
									});
								}, 2000);
							}
						}
					} else {
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
					}
				}
			});
		} else {
			me.userIsLogin = false;
			me.userInfo = {};

			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				success: res => {
					if (res.confirm) {
						// 如果需要强制登录，使用reLaunch方式
						uni.reLaunch({
							url: '../user/login'
						});
					} else {
						uni.reLaunch({
							url: '../user/login'
						});
					}
				}
			});
		}
	},
	methods: {
		bindPickerChangeProject: function(e) {
			var me = this;
			me.projectIndex = e.target.value;
			//storage当前项目更新
			me.currentProjectInfo = me.projectList[me.projectIndex];
			uni.setStorageSync('currentProjectInfo', me.projectList[me.projectIndex]);
			me.getStatistics(me.currentProjectInfo.Id);
			//console.log(me.projectStatistic);
		},
		bindPickerChangeChart: function(e) {
			var me = this;
			me.chartIndex = e.target.value;
			//获取LineChart
			uni.request({
				url: serverUrl + '/PhysicalInventoryOrder/GetLineStatistics/' + me.chartList[me.chartIndex].value,
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							if (res.data.Data == null) {
								uni.showToast({
									title: '暂无统计数据',
									duration: 1500,
									icon: 'none'
								});
							} else {
								//显示chart
								me.projectStatistic = res.data.Data;
								if (me.projectStatistic.TotalLines > 0) {
									me.projectProgress = Math.ceil((me.projectStatistic.CountedLines / me.projectStatistic.TotalLines) * 100);
								} else {
									me.projectProgress = 0;
								}
								if (me.projectStatistic.FirstTotalLines > 0) {
									me.firstProgress = Math.ceil((me.projectStatistic.FirstCountedLines / me.projectStatistic.FirstTotalLines) * 100);
								} else {
									me.firstProgress = 0;
								}
								if (me.projectStatistic.IsSecondEnabled == 1) {
									if (me.projectStatistic.SecondTotalLines > 0) {
										me.secondProgress = Math.ceil((me.projectStatistic.SecondCountedLines / me.projectStatistic.SecondTotalLines) * 100);
									} else {
										me.secondProgress = 0;
									}
								}
								if (me.projectStatistic.IsThirdEnabled == 1) {
									if (me.projectStatistic.FirstTotalLines > 0) {
										me.thirdProgess = Math.ceil((me.projectStatistic.ThirdCountedLines / me.projectStatistic.FirstTotalLines) * 100);
									} else {
										me.thirdProgess = 0;
									}
								}
								//console.log(me.projectStatistic);
							}
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
						//return;
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});

			//console.log(me.projectStatistic);
		},
		getStatistics: function(projectId) {
			let me = this;
			let serverUrl = me.getServerUrl();
			let userInfo = me.getCurrentUserInfo();

			uni.request({
				url: serverUrl + '/PhysicalInventoryOrder/GetStatistics/' + projectId,
				method: 'POST',
				header: {
					appKey: me.appKey,
					appSecret: me.appSecret,
					userId: userInfo.Id,
					openId: userInfo.OpenId
				},
				success: res => {
					if (res.data.Status) {
						if (res.data.StatusCode == 'UserNotLogin') {
							uni.removeStorageSync('currentUserInfo');
							me.userIsLogin = false;
							uni.showToast({
								title: '用户已异地登录',
								duration: 2000,
								icon: 'none'
							});
						} else {
							if (res.data.Data == null) {
								uni.showToast({
									title: '此盘点无统计数据',
									duration: 1500,
									icon: 'none'
								});
								me.projectStatistic = {};
							} else {
								me.projectStatistic = res.data.Data;
								if (me.projectStatistic.TotalLines > 0) {
									me.projectProgress = Math.ceil((me.projectStatistic.CountedLines / me.projectStatistic.TotalLines) * 100);
								} else {
									me.projectProgress = 0;
								}
								if (me.projectStatistic.FirstTotalLines > 0) {
									me.firstProgress = Math.ceil((me.projectStatistic.FirstCountedLines / me.projectStatistic.FirstTotalLines) * 100);
								} else {
									me.firstProgress = 0;
								}
								if (me.projectStatistic.IsSecondEnabled == 1) {
									if (me.projectStatistic.SecondTotalLines > 0) {
										me.secondProgress = Math.ceil((me.projectStatistic.SecondCountedLines / me.projectStatistic.SecondTotalLines) * 100);
									} else {
										me.secondProgress = 0;
									}
								}
								if (me.projectStatistic.IsThirdEnabled == 1) {
									if (me.projectStatistic.FirstTotalLines > 0) {
										me.thirdProgess = Math.ceil((me.projectStatistic.ThirdCountedLines / me.projectStatistic.FirstTotalLines) * 100);
									} else {
										me.thirdProgess = 0;
									}
								}
								//console.log(me.projectStatistic);
							}
						}
					} else {
						//debugger;
						uni.showToast({
							title: res.data.StatusMessage,
							duration: 2000,
							icon: 'none'
						});
						//return;
					}
				},
				fail: function() {
					uni.showToast({
						title: '网络异常请重试',
						duration: 2000,
						icon: 'none'
					});
				},
				complete: function() {
					setTimeout(function() {
						uni.hideLoading();
					}, 2000);
				}
			});
		}
	}
};
</script>

<style>
.progress {
	margin: 30rpx 0;
}
.three {
	border-right: 2rpx solid #f5f6f8;
	border-bottom: 2rpx solid #f5f6f8;
}
.grace-title {
	padding: 10rpx 0;
	line-height: 2em;
	display: flex;
	flex-wrap: nowrap;
	font-size: 28rpx;
	justify-content: space-between;
	font-weight: 500;
	align-items: center;
}
/* #ifdef H5 */
.grace-title {
	font-weight: 700;
}
/* #endif */
.grace-title > .title {
	width: 200rpx;
	flex: auto;
}
.grace-title > .icon {
	width: 50rpx;
	font-size: 50rpx;
}

.current-project {
	margin-left: 10upx;
	width: 170upx;
}

.three > .items {
	width: 33.333333%;
	border-left: 2rpx solid #f5f6f8;
	border-top: 2rpx solid #f5f6f8;
	color: #a5a7b2;
}

.contact {
	width: 100%;
	height: 100rpx;
	line-height: 100rpx;
	text-align: center;
	margin-top: 30rpx;
}

.demoForIcon {
	border-right: 2rpx solid #f5f6f8;
	border-bottom: 2rpx solid #f5f6f8;
}

.demoForIcon > .items {
	border-left: 2rpx solid #f5f6f8;
	border-top: 2rpx solid #f5f6f8;
	color: #a5a7b2;
}

.ucenter-line {
	width: 100%;
	height: 3px;
	background: #f4f5f6;
	margin: 16px 0;
}

.hello {
	display: flex;
	flex: 1;
	flex-direction: column;
}

.title {
	color: #8f8f94;
	margin-top: 50upx;
}

.ul {
	font-size: 30upx;
	color: #8f8f94;
	margin-top: 50upx;
}

.ul > view {
	line-height: 50upx;
}
.percenpading {
	padding-right: 100upx;
}
</style>
